@import url(https://assets.hackclub.com/fonts/Phantom_Sans_0.7/Bold.woff);
* {
  margin: 0;
}
.landing {
  height: 89vh;
  font-family: "Phantom Sans";
  color: #ffffff;
  overflow: hidden;
  box-sizing: content-box;
  background-color: #33d6a6;
  transition: background-color 1000ms linear;
}

body {
  overflow-y: hidden;
  overflow-x: hidden;
}

@media only screen and (max-width: 768px) {
  .landing {
    height: 95vh;
  }
}

.landing__content {
  margin-top: 7%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.landing__content h2 {
  font-size: 2.5rem;
  margin-top: 3%;
  /*cmargin-bottom: ; */
}

.landing__message {
  margin-top: 3%;
}

.landing__comingSoon {
  margin-top: 7%;
  padding-bottom: 7.4%;
}
.navbar {
    overflow: hidden;
    background-color: #ffffff;
}
